<template>
  <div class="singer" v-if="singerList" ref="singer">
    <list-view @select="selectSinger" :data="singerList" ref="list"></list-view>
    <router-view> </router-view>
  </div>
</template>

<script>
import { getSingerList } from "../../api/singer.js";
import { OK } from "../../api/config.js";
import Singer from "../../common/tools/singer.js";
import ListView from "../../base/listView.vue";
// vuexmapMutaions,mutaion
import {mapMutations} from 'vuex'
import {playListMixin} from '../../common/tools/mixin.js'

const HOT_NAME = "";
const HOT_SINGER_LEN = 10;

export default {
  mixins: [playListMixin],
  components: {
    ListView
  },
  data() {
    return {
      singerList: []
    };
  },
  mounted() {
    this._getSingerList();
  },
  methods: {
    selectSinger(singer) {
      this.$router.push({
        path: `/singer/${singer.id}`
      })
      // vuextypesinger
      this.setSinger(singer)
    },
    // 
    _getSingerList() {
      getSingerList().then(res => {
        if (res.code === OK) {
          this.singerList = this._normalizeSinger(res.data.list);
        }
      });
    },
    // 
    _normalizeSinger(list) {
      let map = {
        //  1.0
        hot: {
          title: HOT_NAME,
          items: []
        }
      };
      list.forEach((item, index) => {
        if (index < HOT_SINGER_LEN) {
          map.hot.items.push(
            new Singer({
              id: item.Fsinger_mid,
              name: item.Fsinger_name
            })
          );
        }

        // 2.0
        const key = item.Findex;
        // 
        if (!map[key]) {
          map[key] = {
            title: key,
            items: []
          };
        }
        // 
        map[key].items.push(
          new Singer({
            id: item.Fsinger_mid,
            name: item.Fsinger_name
          })
        );
      });
      // 3.0  map
      let hot = [];
      let ret = [];

      for (let key in map) {
        let val = map[key];
        if (val.title.match(/[a-zA-Z]/)) {
          ret.push(val);
        } else if (val.title === HOT_NAME) {
          hot.push(val);
        }
      }
      // 
      // console.log('----')
      ret.sort((a, b) => {
        return a.title.charCodeAt(0) - b.title.charCodeAt(0);
      });
      return hot.concat(ret);
    },

    ...mapMutations({     //
      //  this.setSingermutations.js
      setSinger: 'SET_SINGER'   //SET_SINGERmutations.js
    }),

    handlePlayList(playList) {
      const bottom = playList.length > 0 ? '60px' : ''
      this.$refs.singer.style.bottom = bottom
      this.$refs.list.refresh()
    }
  }
};
</script>

<style lang="less" scoped>
.singer {
  position: fixed;
  z-index: 100;
  top: 80px;
  bottom: 0;
  width: 100%;
}
</style>


