<template>
  <scroll class="listview" :data="data" ref="listview" :listenScroll="listenScroll" :probeType="probeType" @scrollMove="scroll">
    <ul>
      <li class="list-group" v-for="(group,i) in data" :key="i" ref="listGroup">
        <h2 class="list-group-title">{{group.title}}</h2>
        <ul>
          <li class="list-group-item" v-for="(item,index) in group.items"  :key="index" @click="selectItem(item)">
            <img class="avater" v-lazy="item.avater" alt="">
            <span class="name">{{item.name}}</span>
          </li>
        </ul>
      </li>
    </ul>
    <!--  -->
    <div class="list-shortcut" @touchstart="touchMoveTo" @touchmove="onTouchMove">
      <ul>
        <li class="item" :class="{active: currentIndex===index}" v-for="(item,index) in shortCutList" :key="index" :data-index="index">
          {{item}}
        </li>
      </ul>
    </div>
    <!--  -->
    <div class="list-fixed" v-show="fixedTitle">
      <h1 class="fixed-title">{{fixedTitle}}</h1>
    </div>

  </scroll>
</template>

<script>
import Scroll from "./scroll";
import { getData } from "../common/tools/dom.js";

const ANCHOR_HEIGHT = 18;

export default {
  props: {
    data: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      scrollY: -1,
      currentIndex: 0 //
    };
  },
  components: {
    Scroll
  },
  created() {
    // data
    this.touch = {};
    this.listenScroll = true;
    this.probeType = 3;
    this.listHeight = [];
  },
  computed: {
    shortCutList() {
      return this.data.map(group => {
        return group.title.substr(0, 1);
      });
    },

    // 
    fixedTitle() {
      if (this.scrollY > 0) return '';
      return this.data[this.currentIndex] ? this.data[this.currentIndex].title : ''
    }
    
  },
  methods: {
    // 
    selectItem(item) {
      this.$emit('select',item)
    },
    refresh() {
      this.$refs.listview.refresh()
    },

    touchMoveTo(e) {
      // currentIndex
      if (!e.target.getAttribute("data-index")) return;
      this.currentIndex = parseInt(e.target.getAttribute("data-index"));
      let anchorIndex = getData(e.target, "index");
      //   y
      let firstTouch = e.touches[0];
      this.touch.y1 = firstTouch.pageY; //Y
      this.touch.anchorIndex = anchorIndex; //
      this._scrollTo(anchorIndex);
    },
    onTouchMove(e) {
      let firstTouch = e.touches[0];
      this.touch.y2 = firstTouch.pageY;
      // 
      let delta = ((this.touch.y2 - this.touch.y1) / ANCHOR_HEIGHT) | 0;
      let anchorIndex = parseInt(this.touch.anchorIndex + delta); //
      this._scrollTo(anchorIndex);
    },
    // 
    _scrollTo(index) {
      this.$refs.listview.scrollToElement(this.$refs.listGroup[index], 0);
    },
    // Y
    scroll(pos) {
      this.scrollY = pos.y;
    },
    // 
    _calculateHeight() {
      // 
      this.listHeight = [];
      const list = this.$refs.listGroup;
      let height = 0;
      this.listHeight.push(height);
      // group
      for (let i = 0; i < list.length; i++) {
        let item = list[i];
        height += item.clientHeight;
        this.listHeight.push(height);
      }
    }
  },
  watch: {
    data() {
      // dom
      setTimeout(() => {
        // listGroup
        this._calculateHeight();
      }, 20);
    },
    scrollY(newY) {
      const listHeight = this.listHeight;
      //   newY > 0
      if (newY > 0) {
        this.currentIndex = 0;
        return;
      }
      // 
      for (let i = 0; i < listHeight.length-1; i++) {
        let height1 = listHeight[i];
        let height2 = listHeight[i + 1];

        // Y   newY
        if (-newY >= height1 && -newY < height2) {
          this.currentIndex = i;
          return;
        }
        // console.log(this.currentIndex)
      }
      // -newY
      this.currentIndex = listHeight.length - 2;
    }
  }
};
</script>

<style lang="less" scoped>
@import "../common/style/variable.less";

.listview {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: @color-background;

  .list-group {
    padding-bottom: 30px;

    .list-group-title {
      height: 30px;
      line-height: 30px;
      padding-left: 20px;
      font-size: @font-size-small;
      color: @color-text-l;
      background: @color-highlight-background;
    }

    .list-group-item {
      display: flex;
      align-items: center;
      padding: 20px 0 0 30px;

      .avater {
        width: 50px;
        height: 50px;
        border-radius: 50%;
      }

      .name {
        margin-left: 20px;
        color: @color-text-l;
        font-size: @font-size-medium;
      }
    }
  }

  .list-shortcut {
    position: absolute;
    z-index: 30;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 20px;
    padding: 20px 0;
    border-radius: 10px;
    text-align: center;
    background: @color-background-d;
    font-family: Helvetica;

    .item {
      padding: 3px;
      line-height: 1;
      color: @color-text-l;
      font-size: @font-size-small;

      &.active {
        color: @color-theme;
      }
    }
  }

  .list-fixed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;

    .fixed-title {
      height: 30px;
      line-height: 30px;
      padding-left: 20px;
      font-size: @font-size-small;
      color: @color-text-l;
      background: @color-highlight-background;
    }
  }

  .loading-container {
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
  }
}
</style>


